import React from 'react';
import './topics.css';
import Settings from './settings';
function Topic() {
    return (
        <>
            <section className="topics_sec">
                <h1 id="topic_head">Topics</h1>
                <div className="topic">
                    <div className="topic_icon">
                        <i className="fas fa-business-time fa-3x"></i>
                        <p>Business</p>
                    </div>
                    <div className="topic_icon">
                        <i className="fas fa-film fa-3x"></i>
                        <p>Entertainment</p>
                    </div>
                    <div className="topic_icon">
                        <i className="fas fa-newspaper fa-3x"></i>
                        <p>General</p>
                    </div>
                    <div className="topic_icon">
                        <i className="fas fa-plus-square fa-3x"></i>
                        <p>Health</p>
                    </div>
                    <div className="topic_icon">
                        <i className="fas fa-atom fa-3x"></i>
                        <p>Science</p>
                    </div>
                    <div className="topic_icon">
                        <i className="fas fa-robot fa-3x"></i>
                        <p>Technology</p>
                    </div>
                </div>
                <Settings/>
                
            </section>
        </>
    )
}
export default Topic;