import React from 'react';
import Header from '../components/header.js';
import './moreinfo.css';
function Moreinfo(props){
    function displayoff(){
        document.getElementsByClassName('moreinfo_sec')[0].style.display='none';
    }
    return(
        <section className="moreinfo_sec">
        
        <div className="moreinfo" id="more_details">
            <h1>{props.news.title}</h1>
            <p id="more_text">{props.news.content}</p>
        </div>
        <p id="author">- Written by {props.news.author}</p>
        <div className="back">
            <a href="#news_div" id="backbutton"><button className="btn btn-primary" onClick={displayoff}><i className="fas fa-arrow-left"></i> Back</button></a>
        </div>
        </section>
    )
}
export default Moreinfo