const changeCountry = (state = "in", action) => {
    console.log(action.type);
    switch (action.type) {
        case "IN":
            return action.payload;
        case "CA":
            return "ca";
        case "US":
            return "us";
        case "EN":
            return "en";
        default:
            return "in";
    }
}

export default changeCountry;