import React, { useEffect, useState } from 'react';
import Content from './content.js';
import Moreinfo from '../moreinfo/moreinfo.js';
import Settings_one from '../discover/testing.js';
const API_KEY = "f17e2a3c922441af8afa19d4f60294df";



function News_content(props) {
    let country_name='in';
    const [data, setData] = useState([]);
    useEffect(() => {
        async function get_news() {
            const result = await fetch(`https://newsapi.org/v2/top-headlines?country=${country_name}&apiKey=${API_KEY}`)
                .then(response => response.json())
            setData(result.articles)
            return result;
        }
        get_news();
    }, [])
    
    
    return (
        <>
            <div>
                {data.map((news) => {
                    return (
                        <div key={news.url}>
                            <Content news={news} />
                           
                        </div>
                    )
                })}


            </div>
        </>

    )
}
export default News_content;