import React, { useEffect, useState } from 'react';
import './content.css';
function Content(props) {
    
    return (
        <>
            <div className="news_div" id="news_div">
                <div className="news_img">
                    {props.news.urlToImage?<img src={props.news.urlToImage} alt="" />:<img src="https://assets.thehansindia.com/h-upload/2020/07/12/983391-breaking-news.webp" alt="" />}
                </div>
                <div className="news_content">
                    <h2>{props.news.title}</h2>
                    {props.news.description?<p>{props.news.description}</p>:<p>No content found!</p>}
                    <span id="news_time">News from {props.news.source.name} / {props.news.publishedAt.slice(0,10)}</span>
                </div>
                <a href={props.news.url}>
                    <div className="news_footer">
                        <p id="more">For more Information</p>
                        <p id="footer_tap">Tap here</p>
                    </div>
                </a>
            </div>
        </>
    )
}
export default Content