import React from 'react';
import {useSelector,useDispatch} from 'react-redux';
import {IN} from '../actions';
function Settings() {
    const country = useSelector(state => state.country);
    const isdark = useSelector(state => state.isdark);
    const dispatch  = useDispatch();
    return (
        <div className="setting_section">
            <h1 id="country_topic">Settings</h1>
            <div className="settings_menu">
                <div className="menu_row">
                    <label htmlFor="toggle">Dark Mode</label>
                    <input type="checkbox" name="toggle" id="dark_check"/>
                </div>
                <hr />
                <div className="menu_row">
                    <label htmlFor="country">Select Country</label>
                    <select name="country" id="country" onChange={(e)=>
                        {
                            dispatch({
                                type:IN,
                                payload:e.target.value
                            })
                        }
                    }>
                        <option value="in">India</option>
                        <option value="us">USA</option>
                        <option value="eg">England</option>
                        <option value="ca">Canada</option>
                    </select>
                </div>
                <hr />
                <div className="menu_row">
                    <label htmlFor="lang">Language</label>
                    <select name="lang" id="">
                        <option value="eng">English</option>
                        <option value="hi">Hindi</option>
                    </select>
                </div>
            </div>
        </div>
    )
}

export default Settings;
